address, article, body, div, footer, form, h1, h3, h4, h5, header, html, img, input, label, li, main, nav, p, span, ul {
    box-sizing: border-box !important;
    font-family: LatoRegular, serif;
    margin: 0;
    padding: 0
    }
address, article, footer, header, nav, section {
    display: block
    }
button {
    cursor: pointer
    }
button:hover, input:hover {
    background-color: #202020;
    border-color: #555;
    color: #ddd
    }
button:active, input:focus {
    background-color: #1e1e1e;
    border-color: #666;
    color: #eee
    }
a, a h5, a h5:hover, a:after, a:hover, a:hover:after, button, button:hover, input, input:hover {
    transition: background 0.15s ease-in-out, color 0.15s ease-in-out, border 0.15s ease-in-out
    }
.products-container {
    column-gap: 1.875rem;
    display: grid;
    grid-template-columns: auto auto auto auto;
    margin: 1rem 0 2rem;
    row-gap: 1.875rem
    }
main.analysis .products-container {
    grid-template-columns: auto auto auto
    }
footer {
    background-color: #222;
    font-size: 1.05rem;
    padding-bottom: 1rem;
    padding-top: 1rem
    }
.footer-container {
    display: grid;
    grid-template-areas: "footer-text footer-navigation footer-information footer-contact" "footer-text footer-navigation footer-socials footer-contact";
    grid-template-columns: repeat(4, 1fr);
    margin: 0 auto
    }
.footer-container section {
    padding: 0 1rem
    }
#dark-layer {
    transition: opacity 0.2s
    }
.sidenav {
    transition: width 0.5s
    }
.card {
    background-color: #333;
    border-radius: 0.5rem;
    box-shadow: 0 0.3125rem 0.9375rem -0.1875rem rgba(0, 0, 0, 0.1);
    display: grid;
    grid-template-areas: "image image" "tags tags" "text actions";
    grid-template-columns: auto auto;
    grid-template-rows: auto;
    padding: 1rem
    }
.card-thumbnail img {
    border-radius: 0.5rem;
    width: 100%
    }
.card-stock {
    border-radius: 0.3125rem;
    color: #eee;
    font-size: 0.9375rem;
    line-height: normal;
    margin-left: auto;
    padding: 0 0.5rem 0.125rem;
    position: relative;
    width: fit-content
    }
.card-text a {
    text-decoration: none
    }
.card-title {
    color: #eee;
    font-family: Verdana, sans-serif;
    font-size: 1rem;
    margin: 0.6rem 0
    }
.card-title:hover {
    color: #a56acd
    }
.card-price {
    font-size: clamp(1rem, 2vw, 1.1rem);
    font-weight: 800
    }
.card-rating {
    font-family: LatoRegular, serif;
    font-size: 1rem
    }
.card-rating:before {
    color: #dd5b5b;
    content: "";
    font-family: "Font Awesome 6 Free", serif;
    font-weight: 900
    }
.card-actions {
    align-items: flex-end;
    display: flex;
    height: 100%;
    padding: 0.5rem 0 0
    }
.card-add {
    background-color: #8043aa;
    border: 0;
    border-radius: 50%;
    box-shadow: 0 0.3125rem 0.9375rem -0.1875rem rgba(0, 0, 0, 0.1);
    cursor: pointer;
    flex-direction: column;
    height: 2.25rem;
    justify-content: flex-end;
    margin-left: auto;
    position: relative;
    width: 2.25rem
    }
.card-add:hover {
    background-color: #9144bb
    }
.card-add:before {
    content: "+";
    font-family: "Font Awesome 6 Free", serif;
    font-size: 0.875rem;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%)
    }
.card-add:active {
    bottom: -1px
    }
.footer-container a {
    color: #a56acd;
    margin-bottom: 0.2rem
    }
.footer-container h5 {
    font-size: 1.1rem;
    font-weight: 700;
    margin: 1rem 0 0
    }
.footer-content {
    margin-top: 0.5rem
    }
.footer-socials a {
    background-color: #444;
    border-radius: 50%;
    color: #ddd;
    display: inline-block;
    height: 2.5rem;
    position: relative;
    text-align: center;
    text-decoration: none;
    width: 2.5rem
    }
.footer-socials a:before {
    font-size: 1.5rem;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%)
    }
.footer-contact address * {
    display: block;
    margin-top: 0.2rem
    }
.footer-contact a:hover, .footer-navigation a:hover {
    color: #eee
    }
.footer-socials a:hover {
    color: #a56acd
    }
.reference-link, main.analysis p a, main.analysis p a:after {
    color: #a56acd;
    cursor: pointer;
    transition: color 0.15s ease-in-out
    }
.reference-link:hover, main.analysis a:hover, main.analysis a:hover:after {
    color: #ddd;
    transition: color 0.15s ease-in-out
    }
main.analysis p a:after {
    content: " ";
    font: normal normal normal 0.75rem/1 FontAwesome
    }
footer.analysis {
    border-top: 0.0625rem solid #444;
    box-shadow: 0 -0.1875rem 0.625rem 0.1875rem rgba(0, 0, 0, 0.15)
    }
.discount {
    color: #cb2722;
    text-decoration: line-through
    }
.in-stock {
    background-color: #34d399
    }
.dark-layer-fade {
    display: block !important;
    opacity: 0.5 !important;
    transition: opacity 0.2s !important
    }
.blink {
    animation: blink-animation 1s linear 2
    }
@keyframes blink-animation {
    50% {
        background: #444;
        }
    }